import Api from "../utils/api";
export default {
  name: "Manager",
  data: function data() {
    return {
      token: ""
    };
  },
  methods: {
    click: function click() {
      localStorage.setItem("token", this.token);
      window.location.reload();
    }
  },
  created: function created() {
    this.token = Api.get_token();
  }
};